<template>
  <div class="page-article text-center" v-if="model">
    <div class="d-flex py-3 px-2 border-bottom">
      <div class="iconfont icon-back text-blue"></div>
      <strong class="flex-1 text-ellipsis text-blue pl-2">
        {{model.title}}
      </strong>
      <div class="text-grey fs-xs">
        2019-06-09
      </div>
    </div>
    <div v-html="model.body" class="px-3 body fs-lg"></div>
    <div class="px-3 border-top py-3">
      <div class="d-flex ai-center">
        <i class="iconfont icon-cc-menu-circle"></i>
        <strong class="text-blue fs-lg ml-1">相关资讯</strong>
      </div>
      <div class="pt-2">
        <router-link tag="div" :to="`/articles/${item._id}`" v-for="item in model.related" :key="item._id" class="text-left py-1">
            {{item.title}}
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    id: {
      require: true
    }
  },
  data() {
    return {
      model:null
    }
  },
  methods: {
    async fetch(){
      const res = await this.$http.get(`articles/${this.id}`)
      console.log(res);
      this.model = res.data
    }
  },
  watch: {
    //简洁写法
    id: 'fetch',
    //复杂写法
    // id(){
    //   this.fetch()
    // }
  },
  created() {
    this.fetch()
  },
}
</script>

